import {
    number_format
} from '../../../components/my-components/formater.js'
export default {
    props: {
        goodsList: {
            type: Array,
            default () {
                return [{
                    img: '/static/img/global/tmp.png',
                    goodName: '',
                    color: '',
                    size: '',
                    num: 0,
                    price: '0',
                    saled: 0
                }]
            }
        },
        toggle: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            menuIndex: ''
        }
    },
    watch: {
        toggle() {
            this.menuIndex='';
        }
    },
    methods: {
        showMenu(goodindex) {
            this.menuIndex = 'good-' + goodindex;
        },
        clickMenuItem(goodindex, item, name) {
            this.menuIndex = '';
            this.$emit('click', {
                type: 'menu-item',
                name: name,
                detail: {
                    goodIndex: goodindex,
                    ...item
                }
            })
        },
        clickGood(item) {
            this.$emit('click', {
                type: 'good',
                detail: {
                    val: item
                }
            })
        },
        formatePrice(val) {
            return number_format(val, 2, '.', ',')
        }
    },
}
